import React from "react";
import Person1 from "../Images/person5.jpg";
import Person2 from "../Images/person3.jpg";
import GroupPic from '../Images/LocationImage3.png'
import {
  Link
} from "react-router-dom";
const Home = () => {
  return (
    <>
      <div className="HomePage">
        <div
          class="p-4 p-md-5 mb-4 rounded text-body-emphasis "
          id="HomeCoverImage"
        >
          <div class="col-lg-6 px-0">
            <h1 class="display-4 fst-italic" id="Htitle"  data-aos="zoom-in">
              Discover the World, One Journey at a Time.
            </h1>
            <p class="lead my-3" id="Content"  data-aos="zoom-in">
              Tourists are drawn to India because of its welcoming customs,
              diverse way of life, cultural legacy, and vibrant fairs and
              festivals.
            </p>
            <p class="lead mb-0">
              <a href="#" class="text" id="VisitLink" data-aos="zoom-in">
                Explore More....
              </a>
            </p>
          </div>
        </div>
        <div className="container py-3 mt-5 " id="Chat">
          <div className="col-md-6 col-lg-7 col-xl-7  mx-auto">
            <ul className="list-unstyled">
              <li className="d-flex  mb-4">
                <img
                  src={Person1}
                  alt="avatar"
                  className="d-flex align-self-start me-3 shadow-1-strong"
                  width="72"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black" }}
                  data-aos="zoom-in"
                />
                <div
                  className="card" data-aos="fade-up"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                >
                  <div
                    className="card-header d-flex justify-content-between p-3"
                    style={{ backgroundColor: "#6643b5" }}
                  >
                    <p className="fw-bold mb-0 " style={{color:'white'}}>Natasha</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      Hey Jack, have you heard about the group of tourists
                      planning to visit India?
                    </p>
                  </div>
                </div>
              </li>
              <li className="d-flex justify-content-between mb-4">
                <div
                  className="card w-100 "
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-end p-3"
                    style={{ backgroundColor: "#f70776" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Jack</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      Oh, really? That sounds exciting! But where in India are
                      they planning to go? South, North, West, or East?
                    </p>
                  </div>
                </div>
                <img
                  src={Person2}
                  alt="avatar"
                  className="rounded-circle d-flex align-self-start ms-3 shadow-1-strong"
                  width="80"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black" }}
                  data-aos="zoom-in"
                />
              </li>
              <li className="d-flex  mb-4">
                <img
                  src={Person1}
                  alt="avatar"
                  className="d-flex align-self-start me-3 shadow-1-strong"
                  width="100"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black" }} 
                  data-aos="zoom-in"
                />
                <div
                  className="card"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-between p-3"
                    style={{ backgroundColor: "#6643b5" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Natasha</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      It's a bit confusing for them. Some want to explore the
                      rich culture in the North, while others are drawn to the
                      scenic beauty in the South.
                    </p>
                  </div>
                </div>
              </li>
              <li className="d-flex justify-content-between mb-4">
                <div
                  className="card w-100"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-end p-3"
                    style={{ backgroundColor: "#f70776" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Jack</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      Interesting. I'd suggest they consider the
                      historical landmarks in the West and the vibrant culture
                      in the East too. What do you think?
                    </p>
                  </div>
                </div>
                <img
                  src={Person2}
                  alt="avatar"
                  className="rounded-circle d-flex align-self-start ms-3 shadow-1-strong"
                  width="80"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black" }}
                  data-aos="zoom-in"
                />
              </li>
              <li className="d-flex  mb-4">
                <img
                  src={Person1}
                  alt="avatar"
                  className="d-flex align-self-start me-3 shadow-1-strong"
                  width="100"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black" }}
                  data-aos="zoom-in"
                />
                <div
                  className="card"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-between p-3"
                    style={{ backgroundColor: "#6643b5" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Natasha</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      True, Jack. Each region offers a unique experience. Maybe
                      they can mix it up and explore different parts of India to
                      get a taste of everything.
                    </p>
                  </div>
                </div>
              </li>
              <li className="d-flex justify-content-between mb-4">
                <div
                  className="card w-100"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-end p-3"
                    style={{ backgroundColor: "#f70776" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Jack</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      That's a great idea! They can have a diverse experience,
                      from the majestic Himalayas in the North to the serene
                      beaches in the South.
                    </p>
                  </div>
                </div>
                <img
                  src={Person2}
                  alt="avatar"
                  className="rounded-circle d-flex align-self-start ms-3 shadow-1-strong"
                  width="80"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black"}}
                  data-aos="zoom-in"
                />
              </li>
              <li className="d-flex  mb-4">
                <img
                  src={Person1}
                  alt="avatar"
                  className="d-flex align-self-start me-3 shadow-1-strong"
                  width="100"
                  height="65"
                  style={{ borderRadius: "50%", border: "2px solid black"}}
                  data-aos="zoom-in"
                />
                <div
                  className="card"
                  id="ChatCard"
                  style={{ border: "2px solid black" }}
                  data-aos="fade-up"
                >
                  <div
                    className="card-header d-flex justify-content-between p-3"
                    style={{ backgroundColor: "#6643b5" }}
                  >
                    <p className="fw-bold mb-0" style={{color:'white'}}>Natasha</p>
                  </div>
                  <div className="card-body">
                    <p className="mb-0">
                      Absolutely! I'll share these suggestions with them. It's
                      always good to have options when exploring a diverse
                      country like India.
                    </p>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <h2 className="text-center" style={{ color: "white" }}  data-aos="zoom-in">
            Please Select where to go ?
          </h2>
        </div>

        <div className="container">
          <div class="d-flex flex-column flex-md-row p-4 gap-4 py-md-5 align-items-center justify-content-center">
            <div
              class="list-group list-group-checkable d-grid gap-2 border-0"
              style={{ float: "left" }}
            >
              <Link to="/NorthTour" style={{ textDecoration: "none" }}  data-aos="fade-up">
                <label
                  class="list-group-item rounded-3 py-5 my-2"
                  for="listGroupCheckableRadios1"
                  id="North"
                >
                 
                  <span
                    style={{
                      backgroundColor: "#005792",
                      padding: "6px",
                      borderRadius: "12px",
                      color: "white",
                    }}
                  >
                    North
                  </span>
                  <span class="d-block small my-2" style={{ color: "white" }}>
                    Taj Mahal is a white marble mausoleum built by Mughal
                    Emperor
                  </span>
                </label>
              </Link>

              <Link to="/EastTour" style={{ textDecoration: "none" }}  data-aos="fade-up">
                <label
                  class="list-group-item rounded-3 py-5 my-2"
                  for="listGroupCheckableRadios2"
                  id="East"
                >
                 
                  <span
                    style={{
                      backgroundColor: "#005792",
                      padding: "8px",
                      borderRadius: "12px",
                      color: "white",
                    }}
                  >
                    East
                  </span>
                  <span class="d-block small my-2" style={{ color: "white" }}>
                    The capital city of the Sikkim
                  </span>
                </label>
              </Link>

              <Link to="/SouthTour" style={{ textDecoration: "none" }}  data-aos="fade-up">
                <label
                  class="list-group-item rounded-3 py-5 my-2"
                  for="listGroupCheckableRadios3"
                  id="South"
                >
                
                  <span
                    style={{
                      backgroundColor: "#005792",
                      padding: "8px",
                      borderRadius: "12px",
                      color: "white",
                    }}
                  >
                    South
                  </span>
                  <span class="d-block small my-2" style={{ color: "white" }}>
                    An example of Islamic architecture, Charminar is a beautiful
                    monument
                  </span>
                </label>
              </Link>

              <Link to="/WestTour" style={{ textDecoration: "none" }}  data-aos="fade-up">
                <label
                  class="list-group-item rounded-3 py-5 my-2"
                  for="listGroupCheckableRadios4"
                  id="West"
                >
               
                  <span
                    style={{
                      backgroundColor: "#005792",
                      padding: "8px",
                      borderRadius: "12px",
                      color: "white",
                    }}
                  >
                    West
                  </span>
                  <span class="d-block small my-2" style={{ color: "white" }}>
                    A Gateway to the heart and soul of India
                  </span>
                </label>
              </Link>
            </div>
            <div>
              
            </div>
            <div class="col-10 col-sm-8 col-lg-6">
        <img src={GroupPic} class="d-block mx-lg-auto img-fluid" alt="Bootstrap Themes" width="700" height="500" loading="lazy"  data-aos="zoom-in"/>
        <h2 className="text-center fw-bold" data-aos="zoom-in">Let's Go</h2>
      </div>
          </div>
        
          
        </div>
        
      </div>
    </>
  );
};

export default Home;
