import React from 'react'
import navbarIcon from '../Images/FinalLogo.png'
import {
  Link
} from "react-router-dom";
const NavBar = () => {
  return (
    <div>
      <nav class="navbar navbar-dark" aria-label="Dark offcanvas navbar">
    <div class="container-fluid">
      <Link class="navbar-brand" to="/"><img src={navbarIcon} style={{ width: '100px', height: '55px',marginLeft:'10px' }} id='LogoNav' /></Link>
      <button class="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbarDark" aria-controls="offcanvasNavbarDark" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="offcanvas offcanvas-end text" tabindex="-1" id="offcanvasNavbarDark" aria-labelledby="offcanvasNavbarDarkLabel">
        <div class="offcanvas-header">
        <img id='AdventureImg' style={{width:'55px',height:'55px',padding:'5px',transition:'background-color 1.0s',borderRadius:'10px',cursor:'pointer'}} src='https://cdn-icons-png.flaticon.com/512/5333/5333434.png'/>  <h5 class="offcanvas-title" id="offcanvasNavbarDarkLabel">Adventurer</h5>
          <button type="button" class="btn-close btn-close-white" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body">
          <ul class="navbar-nav justify-content-end flex-grow-1 pe-3">
            <li class="nav-item">
              <Link class="nav-link " aria-current="page" to="/">Home</Link>
            </li>
            <li class="nav-item">
              <Link class="nav-link" to="/Contact">Contact us</Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </nav>
    </div>
  )
}

export default NavBar
