import React from 'react'
import indiaFlag from '../Images/india.png'
const Card = (props) => {
  return (
    <div>
       <div id='card' className="card card-cover h-100 overflow-hidden text-white  rounded-4 "  style={{
              backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${props.Poster})`,
              backgroundSize: 'cover',boxShadow:' rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset'
            }} data-aos="fade-up">
              <div className="d-flex flex-column h-100 p-5 pb-3 text-white text-shadow-1">
                <h2 className="pt-5 mt-5 mb-4 display-6 lh-1 fw-bold" data-aos="zoom-in">{props.name}</h2>
                <ul className="d-flex list-unstyled mt-auto">
                  <li className="me-auto" data-aos="zoom-in">
                    <img src={indiaFlag} alt="Bootstrap" width="32" height="32" className="rounded-circle border border-white" />
                  </li>
                  <li className="d-flex align-items-center me-3" data-aos="zoom-in">
                  <svg style={{color:'white',fontWeight:'bold'}}  xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-geo-alt" viewBox="0 0 16 16">
  <path d="M12.166 8.94c-.524 1.062-1.234 2.12-1.96 3.07A31.493 31.493 0 0 1 8 14.58a31.481 31.481 0 0 1-2.206-2.57c-.726-.95-1.436-2.008-1.96-3.07C3.304 7.867 3 6.862 3 6a5 5 0 0 1 10 0c0 .862-.305 1.867-.834 2.94zM8 16s6-5.686 6-10A6 6 0 0 0 2 6c0 4.314 6 10 6 10z"/>
  <path d="M8 8a2 2 0 1 1 0-4 2 2 0 0 1 0 4zm0 1a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
</svg> 
                    <small style={{color:'white',fontWeight:'bold'}}> - {props.location}</small>
                  </li> 
                 
                </ul>
              </div>
            </div>
    </div>
  )
}

export default Card
